import * as React from 'react';
import { TileLayout } from '@progress/kendo-react-layout';
import RadialBarChart from '../charts/RadialBarChart';
import PolarChart from '../charts/PolarChart';
import '../charts/TileChart.css';

const tiles = [{
  defaultPosition: {
    col: 1,
    colSpan: 4,
    rowSpan: 3
  },
  header: "Winter Olympic medals per existing country (TOP 5)",
  body: <RadialBarChart />
}, {
  defaultPosition: {
    col: 2,
    colSpan: 1,
    rowSpan: 1
  },
  header: "Tile 2",
  body: <p>Reorderable and horizontally resizable only tile.</p>,
//   resizable: 'horizontal'
}, {
  defaultPosition: {
    col: 7,
    colSpan: 4,
    rowSpan: 3
  },
  header: "Sales of petroleum products March, Norway",
  body: <PolarChart />,
//   resizable: 'vertical'
}, {
  defaultPosition: {
    col: 4,
    colSpan: 1,
    rowSpan: 2
  },
  header: "Tile 4",
  body: <p>Resizable only.</p>,
//   reorderable: false
}, {
  defaultPosition: {
    col: 5,
    colSpan: 1,
    rowSpan: 2
  },
  header: "Tile 5",
  body: <p>Reorderable and horizontally resizable only tile.</p>,
}, {
  defaultPosition: {
    col: 6,
    colSpan: 1,
    rowSpan: 1
  },
  header: "Tile 6",
  body: <p>Reorderable and vertically resizable only tile.</p>,
//   resizable: 'vertical'
}, {
  defaultPosition: {
    col: 7,
    colSpan: 1,
    rowSpan: 2
  },
  header: "Tile 7",
  body: <p>Resizable only.</p>,
//   reorderable: false
}, {
  defaultPosition: {
    col: 8,
    colSpan: 1,
    rowSpan: 1
  },
  header: "Tile 8",
  body: <p>Reorderable and vertically resizable only tile.</p>,
//   resizable: 'vertical'
}, {
  defaultPosition: {
    col: 9,
    colSpan: 1,
    rowSpan: 2
  },
  header: "Tile 9",
  body: <p>Resizable only.</p>,
//   reorderable: false
}, {
  defaultPosition: {
    col: 10,
    colSpan: 1,
    rowSpan: 1
  },
  header: "Tile 10",
  body: <p>Reorderable and vertically resizable only tile.</p>,
//   resizable: 'vertical'
}, {
  defaultPosition: {
    col: 11,
    colSpan: 1,
    rowSpan: 2
  },
  header: "Tile 11",
  body: <p>Resizable only.</p>,
//   reorderable: false
}, {
  defaultPosition: {
    col: 12,
    colSpan: 1,
    rowSpan: 2
  },
  header: "Tile 12",
  body: <p>Resizable only.</p>,
//   reorderable: false
}];
const TileLayoutComponent = props => {
  const { dir } = props;
  return <TileLayout dir={dir} columns={12} rowHeight={150} gap={{
    rows: 10,
    columns: 10
  }} items={tiles} />;
};

export default TileLayoutComponent;